// 页面顺序配置，与菜单顺序一致
export const PAGE_ORDER = [
  { path: '/introduction', label: 'nav.introduction' },
  { path: '/quick-start', label: 'nav.quickStart' },
  { path: '/faq', label: 'nav.faq' },
  // { path: '/changelog', label: 'nav.changelog' },
  { path: '/transcode-config', label: 'nav.transcodeConfig' },
  { path: '/framework-prompt-config', label: 'nav.frameworkPromptConfig' },
  { path: '/selection-grouping-sampling', label: 'nav.selectionGroupingSampling' },
  { path: '/component-mapping', label: 'nav.componentMapping' },
  { path: '/module-marking', label: 'nav.moduleMarking' },
  // { path: '/architecture', label: 'nav.architecture' },
]

export const getPageNavigation = (currentPath: string) => {
  const currentIndex = PAGE_ORDER.findIndex(page => page.path === currentPath)
  
  if (currentIndex === -1) {
    return { prev: undefined, next: undefined }
  }

  return {
    prev: currentIndex > 0 ? PAGE_ORDER[currentIndex - 1] : undefined,
    next: currentIndex < PAGE_ORDER.length - 1 ? PAGE_ORDER[currentIndex + 1] : undefined,
  }
}
