import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism'

interface CodeBlockProps {
  language: string
  children: string
  className?: string
}

const CodeBlock = ({ language, children, className }: CodeBlockProps) => {
  return (
    <div className={`my-6 rounded-lg overflow-hidden shadow-lg ${className}`}>
      {/* IDE 风格标题栏 */}
      <div className="flex items-center justify-between px-4 py-2 bg-[#282c34] border-b border-gray-700">
        <div className="flex items-center gap-2">
          {/* 三色按钮 */}
          <div className="w-3 h-3 rounded-full bg-[#ff5f56]"></div>
          <div className="w-3 h-3 rounded-full bg-[#ffbd2e]"></div>
          <div className="w-3 h-3 rounded-full bg-[#27ca40]"></div>
        </div>
        {language && <span className="text-xs text-gray-400 font-mono">{language}</span>}
      </div>
      {/* 代码内容 */}
      <SyntaxHighlighter
        style={oneDark}
        language={language || 'text'}
        PreTag="div"
        customStyle={{
          margin: 0,
          borderRadius: 0,
          padding: '1rem',
          fontSize: '14px',
        }}
      >
        {children.replace(/\n$/, '')}
      </SyntaxHighlighter>
    </div>
  )
}

export default CodeBlock
