import { Button } from 'antd'
import { LeftOutlined, RightOutlined } from '@ant-design/icons'
import { useNavigate } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

interface PageInfo {
  path: string
  label: string
}

interface PageNavigationProps {
  prev?: PageInfo
  next?: PageInfo
}

const PageNavigation = ({ prev, next }: PageNavigationProps) => {
  const navigate = useNavigate()
  const { t } = useTranslation()

  if (!prev && !next) {
    return null
  }

  return (
    <div className="flex justify-between items-stretch gap-4 pt-12 mt-12 border-t border-gray-200">
      <div className="flex-1 max-w-md">
        {prev && (
          <Button
            type="default"
            size="large"
            onClick={() => navigate(prev.path)}
            className="w-full h-auto !py-3 hover:!text-[#F07D18] hover:!border-[#F07D18] transition-colors"
          >
            <div className="flex items-center justify-start gap-2">
              <LeftOutlined className="text-base flex-shrink-0" />
              <div className="flex flex-col items-start text-left">
                <span className="text-xs text-gray-500 leading-tight">{t('common.prevPage')}</span>
                <span className="text-sm font-medium leading-tight mt-0.5">{t(prev.label)}</span>
              </div>
            </div>
          </Button>
        )}
      </div>

      <div className="flex-1 max-w-md flex justify-end">
        {next && (
          <Button
            type="default"
            size="large"
            onClick={() => navigate(next.path)}
            className="w-full h-auto !py-3 hover:!text-[#F07D18] hover:!border-[#F07D18] transition-colors"
          >
            <div className="flex items-center justify-end gap-2">
              <div className="flex flex-col items-end text-right">
                <span className="text-xs text-gray-500 leading-tight">{t('common.nextPage')}</span>
                <span className="text-sm font-medium leading-tight mt-0.5">{t(next.label)}</span>
              </div>
              <RightOutlined className="text-base flex-shrink-0" />
            </div>
          </Button>
        )}
      </div>
    </div>
  )
}

export default PageNavigation
